
.skills {
  display: grid;

  background-color: #ffffff;

  @media (min-width: $medium + 1) {
    padding: $gutter / 2;

    grid-gap: $gutter / 2;
    grid-template-columns: repeat(4, 1fr);
  }

  @media (max-width: $medium) and (min-width: $small + 1) {
    padding: $gutter / 2;

    grid-gap: $gutter / 2;
    grid-template-columns: repeat(3, 1fr);
  }

  @media (max-width: $small) and (min-width: $xsmall + 1) {
    padding: $gutter / 2;

    grid-gap: $gutter / 2;
    grid-template-columns: 1fr 1fr;
  }

  @media (max-width: $xsmall) {
    padding: $gutter;

    grid-gap: $gutter;
    grid-template-columns: 1fr;
  }

  .btn-set {
    @media (max-width: $small) and (min-width: $xsmall + 1) {
      padding: ($gutter / 2) 0 ($gutter / 2 * 3);
    }

    @media (max-width: $xsmall) {
      padding: 0;
    }
  }

  .btn-accent {
    @media (max-width: $xsmall) {
      margin: 0;
    }
  }
}

.skill-item {
  position: relative;

  display: grid;
  overflow: hidden;

  padding: $gutter;

  cursor: pointer;

  align-items: center;
  justify-content: center;

  @media (min-height: 691px) {
    min-height: calc(33.33vh - #{$gutter} * 0.66);
  }

  @media (max-height: 690px) {
    min-height: calc(230px - #{$gutter} * 0.66);
  }


  &__overlay {
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    transition: opacity 0.5s;

    opacity: 0.8;
    background-color: $base-color;
  }

  &__image {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    overflow: hidden;

    transition: transform 0.5s;

    background-position: 50% 50%;
    background-size: cover;
  }

  &__title {
    z-index: 1;

    transition: color 0.5s;
    letter-spacing: 0.1em;

    color: $light-color;

    font-size: 1.25rem;
    font-weight: 700;
  }

  &:hover {
    .skill-item__overlay {
      opacity: 0.95 !important;
    }

    .skill-item__image {
      transform: scale(1.1);
    }

    .skill-item__title {
      color: $accent-color;
    }
  }

  &:nth-of-type(1) {
    @media (min-width: $medium + 1) {
      grid-column: 1 / span 2;
    }

    @media (max-width: $medium) and (min-width: $small + 1) {
      grid-column: 1 / span 2;
    }

    @media (max-width: $small) and (min-width: $xsmall + 1) {
      grid-column: 2;
      grid-row: 1 / span 2;
    }
  }

  &:nth-of-type(4) {
    @media (max-width: $medium) and (min-width: $small + 1) {
      grid-column: 2 / span 2;
    }

    @media (max-width: $small) and (min-width: $xsmall + 1) {
      grid-column: 1 / span 2;
    }
  }

  &:nth-of-type(5) {
    @media (min-width: $medium + 1) {
      grid-column: 2 / span 2;
    }

    @media (max-width: $medium) and (min-width: $small + 1) {
      grid-column: 1 / span 2;
    }

    @media (max-width: $small) and (min-width: $xsmall + 1) {
      grid-column: 1;
      grid-row: 4 / span 2;
    }
  }

  &:nth-of-type(6) {
    @media (min-width: $medium + 1) {
      grid-column: 4;
      grid-row: 2 / span 2;
    }

    @media (max-width: $medium) and (min-width: $small + 1) {
      grid-column: 3;
      grid-row: 3 / span 2;
    }
  }

  &:nth-of-type(7) {
    @media (max-width: $medium) and (min-width: $small + 1) {
      grid-column: 1;
      grid-row: 4 / span 2;
    }
  }

  &:nth-of-type(9) {
    @media (max-width: $medium) and (min-width: $small + 1) {
      grid-column: 2 / span 2;
    }
  }
}
